function ContentId3StageSecond() {

  const [showModal, setShowModal] = useState(false);

  const handleImageClick = () => {
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
  };


  const checkContents = [
    '  3     6     .(,  .)',
    '       .',
    '     15       .( ,  3 ~    15    .)',
    '   1         .'
  ];
  const [checkboxes, setCheckboxes] = useState([false, false, false, false]);
  const [canProceed, setCanProceed] = useState(false);

  const [isSecondVisible, setIsSecondVisible] = useState(false);
  const [activeKey, setActiveKey] = useState('0')

  const handleAccordionChange = (eventKey) => {
    setActiveKey(eventKey);
  };

  const handleCheckboxChange = (index) => {
    const updatedCheckboxes = [...checkboxes];
    updatedCheckboxes[index] = !updatedCheckboxes[index];
    setCheckboxes(updatedCheckboxes);

    const allChecked = updatedCheckboxes.every((checkbox) => checkbox);
    setCanProceed(allChecked);
  };

  const handleProceed = () => {
    if (canProceed) {
      // Proceed to the next step
      setIsSecondVisible(true);
      setActiveKey('1')
    } else {
      alert('  .');
    }
  };

  return (
    <>
      <div className="detail-header">
        2      
      </div>
      <Accordion  activeKey={activeKey} onSelect={handleAccordionChange}>
        <Accordion.Item eventKey="0">
          <Accordion.Header><div className='accordion-header-font'>1.       .   . </div></Accordion.Header>
          <Accordion.Body>
            <Form>
              {
                isSecondVisible ?
                  checkboxes.map((seq, index) => (
                    <div key={index} className='accordion-body-font'>
                      <Form.Check
                        disabled
                        type="checkbox"
                        checked={seq}
                        label={checkContents[index]}
                        onChange={() => handleCheckboxChange(index)}
                      />
                    </div>
                  ))
                  :
                  checkboxes.map((seq, index) => (
                    <div key={index} className='accordion-body-font'>
                      <Form.Check
                        type="checkbox"
                        checked={seq}
                        label={checkContents[index]}
                        onChange={() => handleCheckboxChange(index)}
                      />
                    </div>
                  ))
              }
            </Form>
            {
              isSecondVisible ? null : <Button onClick={handleProceed}></Button>
            }
          </Accordion.Body>
        </Accordion.Item>
        {
          isSecondVisible ?
            <Accordion.Item eventKey="1">
              <Accordion.Header><div className='accordion-header-font'>2.    tip  .<br />(    .)</div></Accordion.Header>
              <Accordion.Body>
                <PC><LaborContractStage /></PC>
                {/* <PC><LaborContractStageMobile /></PC> */}
                <Mobile>
                  <div><img src="/L-001.png" alt="" className='image-body' onClick={handleImageClick} /></div>
                  {showModal && (
                    <ImageModal imageUrl="/L-001.png" onClose={handleCloseModal} />
                  )}
                </Mobile>
              </Accordion.Body>
            </Accordion.Item>
            : null
        }
      </Accordion>
    </>
  )
};

