import { Accordion, Table, ButtonGroup, ToggleButton, Badge } from 'react-bootstrap';
import ApplicationFormTen from '../applicationform/ApplicationFormTen';
import { PersonalInfoAgreement } from '../applicationform/PersonalInfoAgreement';

function ContentId10StageFirst() {

  const commuteManage = [
    { id: "0", system: "()", operation: "  ,      ", cost: "20~30" },
    { id: "1", system: "", operation: "        (    ", cost: "20~50" },
    { id: "2", system: "RF ", operation: " ID      (  RF    ", cost: "20~50" },
    { id: "3", system: "()", operation: "      App  ", cost: "500~2000/1( )" },
    { id: "4", system: "(WIFI)", operation: "     App   ", cost: "500~2000/1( )" },
    { id: "5", system: "(GPS)", operation: "      App   ", cost: "500~2000/1( )" }
  ];

  return (
    <>
      <div className="detail-header">
        1         
      </div>
      <Accordion defaultActiveKey={["0"]}>
        <Accordion.Item eventKey="0">
          <Accordion.Header>
            <div>1.    .</div>
          </Accordion.Header>
          <Accordion.Body>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {
                  commuteManage.map((item, idx) => {
                    return (
                      <tr key={item.id}>
                        <td>{item.id}</td>
                        <td>{item.system}</td>
                        <td>{item.operation}</td>
                        <td>{item.cost}</td>
                      </tr>
                    )
                  })
                }
              </tbody>
            </Table>

          </Accordion.Body>
        </Accordion.Item>


        <Accordion.Item eventKey="1">
          <Accordion.Header>
            <div>2.     .</div>
          </Accordion.Header>
          <Accordion.Body>
            <PersonalInfoAgreement />
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </>
  )
}

